import { Component } from '@angular/core';
import { NgxSmartModalService, NgxSmartModalComponent } from 'ngx-smart-modal'
import { ModalInstance } from 'ngx-smart-modal/src/services/modal-instance';
import { DataTrasferService } from 'app/shared/services';
import { ApiService } from '@app/core';
import { DomSanitizer } from '@angular/platform-browser';

declare var window: Window;

interface Window {
  webkitURL?: any;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  // infoModal : any = {}
  title = 'Co Lendng | Master | Home';
  getModal = true
  mtitle = 'Internal Server Error'
  mMsg = 'Unknown Error, Please contact Admin Team'
  mBtn = 'OK';
  confirm: any;
  needFor: any;
  imgLink: any;
  image: any;
  constructor(
    public modalService: NgxSmartModalService,
    public serviceDataTransfer: DataTrasferService,
    private apiService: ApiService,
    private sanitizer: DomSanitizer
  ) {

  }

  ngAfterViewInit() {
    var self = this
    this.modalService.getModal('infoModal').onDataAdded.subscribe((modal: NgxSmartModalComponent) => {
      console.log(modal);
      self.mtitle = modal['title'] ? modal['title'] : self.mtitle;
      self.mMsg = modal['msg'] ? modal['msg'] : self.mMsg;
      self.confirm = modal['confirmation'];
      self.needFor = modal['purpose'];
      self.imgLink = modal['imageUrl'];
      this.image = '';
      if (modal['imageUrl']) {
        this.getImg(modal['imageUrl']);
        this.confirm = 'No need';
      }
    }); 
  }

  getConfirmation(val) {
    if (this.needFor == 'LogOut') {
      if (val == 'yes') {
        this.serviceDataTransfer.changeData('ok');
      }
    }
    if (this.needFor == 'acknowledgment') {
      if (val == 'yes') {
        this.serviceDataTransfer.changeData(val);
      }
    }
  } 

  getImg(val) {
    this.apiService.getImage('underwriting', '/fetchKycDocs', val).subscribe(res => { 
      let blob = new Blob( [  new Uint8Array( res.data.data ) ], { type: "image/png" } );
      let urlCreator = window['URL'] || window.webkitURL; 
      let imageVal = urlCreator.createObjectURL( blob ); 
      this.image = this.sanitizer.bypassSecurityTrustUrl(imageVal);
    });
  }

}
